body {
  background-color: #9ed1fc;
  color: whitesmoke;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  justify-content: center;
  position: relative;
  text-align: center;
}

.title {
  font-weight: 900;
  font-size: 60px;
  margin: 0;
  text-shadow: -1px 0 #93aac2, 0 1px slategray, 1px 0 slategray, 0 -1px slategray;
}

.top-buttons {
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin-left: 5%;

  #instructions {
    width: 200px;
  }

  #new-game {
    width: 200px;
  }
}

.canvas-container {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  margin-top: 2%;
  min-width: 700px;
  width: 100%;
}

#canvas {
  border: 5px solid #ebf0f3;
  border-radius: 10px;
  width: 90%;
}

.startscreen-container {
  display: flex;
  justify-content: center;
  position: absolute;
  width: 100%;
}

#startscreen {
  align-items: center;
  background-color: #6899e4;
  border: 3px solid whitesmoke;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 400px;
  padding: 5%;
  width: 40vw;

  h1 {
    margin-top: 0;
  }

  ul {
    list-style: none;
    margin-bottom: 5%;
    margin-top: 0;
    padding: 0;
  }
}

.volume {
  position: absolute;
  right: 5%;
  top: 0;
}

#volume {
  cursor: pointer;
  font-size: 48px;
}

#volume:hover {
  color: cornflowerblue;
}

.buttons {
  cursor: pointer;
  height: 36px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 800;
}

.buttons button:hover {
  color: cornflowerblue;
}

.socials ul {
  align-items: flex-end;
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin-top: 0;
  width: 90vw;
}

.social-link {
  margin: 0 10px;
}

a {
  cursor: pointer;
  font-size: 72px;
}

a:hover {
  color: cornflowerblue;
}

a:last-child {
  display: flex;
  position: relative;
  top: 2px;
  left: 20px;
}

.enable {
  display: block !important;
}

.game-over {
  position: absolute;
}

.game-over-messages {
  align-items: center;
  background-color: #658fdd;
  border: 3px solid whitesmoke;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 36px;
  font-weight: 700;
  justify-content: center;
  padding: 5%;
  width: 30vw;

  h5 {
    margin-top: 0;
  }
}

#button:hover {
  background-color: #bbcbe9;
}

button {
  border: none;
  border-radius: 5px;
  color: #4f80b1;
  cursor: pointer;
  font-size: 22px;
  font-weight: 700;
  padding: 10px;
}

button:hover {
  background-color: #bbcbe9;
}

#intro {
  padding-bottom: 20px;
}
